.Toastify__toast-container {
    width: 100%;
    padding: 0 20px;
    max-width: 550px;
    top: 20px;
}

.Toastify__toast {
    padding: 20px;
}

.Toastify__close-button {
    outline: 0;

    &:focus {
        outline: 0;
    }
}

.Toastify__toast-body {
    padding-right: 10px;
}